<template>
  <div>
    <CRow>
      <CCol sm="5">
        <CCard>
          <CCardBody>
            <CAlert show color="warning">
              <span class="yellowTitle">
                <CInput type="email" autocomplete="email" label="帳號 " placeholder="Enter Billing Account" required was-validated />
              </span>
            </CAlert>
            <CSelect label="連結帳戶" :options="option" class="billingSelect" />
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>

    <CCard>
      <CCardBody>
        <CDataTable :items="project_items" :fields="project_fields" table-filter items-per-page-select :items-per-page="10" hover sorter pagination>
          <template #show_details="{item, index}">
            <td class="py-2 checkboxType authority">
              <input type="checkbox" :id="item.projectNumber + '1'" value="first_checkbox" />
              <label for="cbox1">檢視</label>
              <!-- <input type="checkbox" :id="item.projectNumber + '2'" value="two_checkbox" />
              <label for="cbox2">編輯</label> -->

              <!-- <CButton color="primary" variant="outline" square size="sm" @click="toggleDetails(item, index)">


                {{ Boolean(item._toggled) ? 'Hide' : 'Show' }}
              </CButton> -->
            </td>
          </template>
          <template #details="{item}">
            <CCollapse :show="Boolean(item._toggled)" :duration="collapseDuration">
              <CCardBody>
                <CDataTable :items="project_items" :fields="project_fields" :items-per-page="10" hover sorter pagination> </CDataTable>
              </CCardBody>
            </CCollapse>
          </template>
        </CDataTable>
      </CCardBody>
      <CCardFooter>
        <div class="footerButton">
          <CButton block variant="outline" color="dark" class="auth_button ">送出</CButton>
        </div>
      </CCardFooter>
    </CCard>
  </div>
</template>
<script>
const items = [
  { account: 'A account', accountID: '0158EE-767756-98C834', cost: '$ 120.00' },
  { account: 'B account', accountID: '0538EE-794636-72C268', cost: '$ 890.00' },
];

export default {
  name: 'CostTable',

  data() {
    return {
      project_items: [
        { project: 'projectTest', projectID: 'projectTest', projectNumber: '8516359843', authority: '$ 120.00' },
        { project: 'myProject', projectID: 'myProject', projectNumber: '6261291528', authority: '$ 0.05' },
      ],
      // items: [
      //   { account: 'A account', accountID: '0158EE-767756-98C834', cost: '$ 120.00' },
      //   { account: 'B account', accountID: '0538EE-794636-72C268', cost: '$ 890.00' },
      // ],
      option: ['A', 'B', 'C'],
      // , '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'
      items: items.map((item, id) => {
        return { ...item, id };
      }),
      details: [],
      collapseDuration: 0,
    };
  },
  computed: {
    computedItems() {
      return items.map((item) => {
        return {
          ...item,
          registeredTimestamp: item.registered.timestamp,
          registeredDate: item.registered.date,
        };
      });
    },
    dateFilteredItems() {
      return this.computedItems.filter((item) => {
        const date = item.registeredTimestamp;
        return date >= this.startDate && date <= this.endDate;
      });
    },
    project_fields() {
      return [
        {
          key: 'project',
          label: this.$t('billing.project'),
        },

        {
          key: 'projectID',
          label: this.$t('billing.projectID'),
        },
        {
          key: 'projectNumber',
          label: this.$t('billing.projectNumber'),
        },

        {
          key: 'show_details',
          label: '權限',
          _style: 'width:1%',
          sorter: false,
          filter: false,
        },
      ];
    },
    // fields() {
    //   return [
    //     {
    //       key: 'account',
    //       label: '連結帳戶',
    //     },
    //     {
    //       key: 'accountID',
    //       label: '帳單帳戶ID',
    //     },

    //     {
    //       key: 'cost',
    //       label: this.$t('billing.cost'),
    //     },
    //     {
    //       key: 'show_details',
    //       label: '',
    //       _style: 'width:1%',
    //       sorter: false,
    //       filter: false,
    //     },
    //   ];
    // },
  },
  methods: {
    setDateFilter(e, end) {
      if (end) {
        this.endDate = new Date(e.target.value).getTime();
      } else {
        this.startDate = new Date(e.target.value).getTime();
      }
    },

    toggleDetails(item) {
      // console.log('item:', item);
      this.$set(this.items[item.id], '_toggled', !item._toggled);
      this.collapseDuration = 300;
      this.$nextTick(() => {
        this.collapseDuration = 0;
      });
    },
  },
};
</script>
